body{
    margin:0;
    padding:0;
    background-size: cover;
    font-family: sans-serif;
    }
.log{  width:1366px;
    height:696px;
    
    }
.loginbox{  width: 520px;
    height: 500px;
    background:#abc;
    color: #fff;
    top:20%;
    left:36%;
    position:absolute;
    box-sizing: border-box;
    padding: 70px 30px;
    } 
.loginbox p{  margin:0;
    padding: 0;
    font-weight:bold;
            }
.loginbox input{width: 100%;
    margin-bottom:20px;
                }
.loginbox input[type="text"], input[type="password"]{
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline:none;
    height:40px;
    color:#fff;
    font-size:16px;
        }
.loginbox input[type="submit"]{
    border: none;
    outline:none;
    height:40px;
    background:#366578;
    color: #fff;
    font-size:18px;
    border-radius:20px;
        }
.loginbox input[type="submit"]:hover
{    cursor:pointer;
    background:#ffc107;
    color:#000;
}
.loginbox a{   
    text-decoration:none;
    font-size:12px;
    line-height:20px;
    color: darkgrey;
}
.loginbox a:hover
{  
    color:#ffc107;
}
.active{
    color:#fff;
    background:#e02626;
    border-radius:4px;
}